import {  Button } from 'reactstrap';
import React, { Component } from 'react';
import { Table, Thead, Tbody, Tr, Th, Td } from "react-super-responsive-table";


export default class TableShopping extends React.Component {

  render() {
    return (
      <Table responsive>
        <thead>
          <tr>
            <th>#</th>
            <th>Nombre Tour</th>
            <th>Descripción</th>
            <th>Cantidad</th>
            <th>Precio Unitario</th>
            <th>Precio Usuario</th>
            <th>Eliminar</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <th scope="row">1</th>
            <td>Cerro Esp Santo</td>
            <td>Montanna</td>
            <td>1</td>
            <td>15000</td>
            <td>15000</td>
            <td><Button color="warning">Remover</Button>{' '}</td>
          </tr>
          <tr>
            <th scope="row">2</th>
            <td>Monte Redondo</td>
            <td>Montanna</td>
            <td>2</td>
            <td>5000</td>
            <td>10000</td>
            <td><Button color="warning">Remover</Button>{' '}</td>
          </tr>
          <tr>
            <th scope="row">3</th>
            <td>Fraijanes</td>
            <td>Cuenta con Parqueo</td>
            <td>3</td>
            <td>6000</td>
            <td>18000</td>
            <td><Button color="warning">Remover</Button>{' '}</td>
          </tr>
        </tbody>
      </Table>
    );
  }
}